import BetterScroll from "better-scroll";
import { fromNowFilter } from "./../utils/mixins";
import { Toast } from "./../utils/helpers";
import adminAPI from "./../apis/admin.js";

export default {
  name: "adminTweetList",
  mixins: [fromNowFilter],
  data() {
    return {
      allTweets: {},
    };
  },
  mounted() {
    this.fetchAllTweets();
  },
  methods: {
    async fetchAllTweets() {
      try {
        const response = await adminAPI.getTweetList();
        this.allTweets = response.data;
        await this.movefunction(100);
      } catch (error) {
        console.log(error);
        Toast.fire({
          icon: "warning",
          title: "讀取資料失敗,請稍後再試!",
        });
      }
    },
    movefunction(s) {
      return new Promise(function (resolve) {
        setTimeout(() => {
          resolve(
            new BetterScroll(".admin_tweet_list", {
              mouseWheel: true, //開啟滑鼠滾動
              disableMouse: false, //關閉滑鼠拖動
              disableTouch: false, //關閉手指觸摸
              scrollX: true, //X軸滾動開啟
              click: true,
            })
          );
        }, s);
      });
    },
    async deleteTweet(id) {
      try {
        const { data } = await adminAPI.deleteTweet(id);
        Toast.fire({
          icon: "success",
          title: data.message,
        });
        this.fetchAllTweets();
      } catch (error) {
        Toast.fire({
          icon: "warning",
          title: error.message,
        });
      }
    },
  },
};
